
.intro {

	&.single .section__main .block { background: none; }
	& .artist__content { display: none; }
	& .event__content { display: none; }
	& .block__header .-side { visibility: hidden; }
	& .block__header.-over { background: none; }
}

.hero {
	position: relative;
	z-index: 100;

	display: none;
	overflow: hidden;

	margin-bottom: 40px;

	&__overlay {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);

		width: 100%;

		text-align: center;
		font-size: 3.6rem;

		opacity: .75;
		color: #fff;
	}
}

@media (--to-medium) {
	.hero img {
		position: relative;

		height: 600px;
		width: auto;

		max-width: none;
		max-height: 100vw;


		transform: translateX(-50%);
		left: 50%;
	}
}

@media (--small) {
	.hero__overlay { font-size: 5rem; }
}

@media (--medium) {
	.hero { display: block; }
	.hero__overlay { font-size: 8rem; }
}
