.event {

	&__header {
		width: 100%;
		margin: 0 auto 30px;
		padding-top: 10px;
		padding-bottom: 10px;

		text-align: center;
	}

	&__intro {
		max-width: 30em;
		margin: 30px auto 0;
	}

	&__footer {
		width: 100%;
		margin: 0 auto;
	}

	& .artist__title { position: relative; }
}

.event-list .event {
	margin-bottom: 40px;
}

@media (--wide) {
	.event {
		&__footer {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
		}
	}
}

/* Event block hover styles */
.home .-focus {

	& .block__header { background-color: #111; }
	& .block__content-wrapper { background-color: #111; }

	& .event__intro {	opacity: 0;	}

	color: var(--color-white);
	& a { color: var(--color-white); }
}
