
button {
  display: inline-block;
  background-color: transparent;
  background: transparent;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  overflow: visible;

  font-family: var(--base-font-family);
  font-weight: 400;
  text-transform: uppercase;

	&:focus {
    text-decoration: none;
	}

	&::-moz-focus-inner {
    border: 0;
    padding: 0;
	}
}

/*
 * Link button class for artist items
 */
.link-button {
  padding-bottom: 3px;
  border-bottom: 1px solid;
  display: inline-block;

  margin: 0 10px 10px;
}

.ticket-button {
  display: block;
  padding: 10px;
  border: 1px solid;
}

a.button {
  position: relative;
  float: left;

  display: block;
  padding-bottom: 2.2em;

  border: none;

  word-break: break-all;
  word-break: break-word;

  &:not(.button--disabled):after {
    content: '\2190';

    position: absolute;
    bottom: 6px;
    display: block;

    font-size: 1.5em;
  }
}

.supports_no-touchevents a.button:hover { opacity: .4; }
.supports_no-touchevents a.button--disabled:hover { opacity: 1; }

a.button--right {
  float: right;
  text-align: right;

  &:not(.button--disabled):after {
    content: '\2192';
    right: 0;
  }
}

a.button--top:after {
  content: '\2191';
}

a.button--bottom:after {
  content: '\2193';
}

.button-wrapper {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px solid;
}

.meta-block-wrapper + .button-wrapper {
  margin-top: 10px;
}

.left { float: left; }
.right { float: right; }

@media (--normal) {

  .link-button { margin: 0 20px 15px; }

  .event__footer .link-button {
    color: #fff;
    opacity: 0;
    visibility: hidden;
  }

  .-focus .event__footer .link-button {
    opacity: 1;
    visibility: visible;
  }

  .button-wrapper {
    margin-top: 42px;
  }
}

a.-back {
  &:before {
    content: '\2190';
    margin-right: 5px;
    position: relative;
    top: -1px;
  }
}


/*
 * Visible only on artist block
 */

.u-section-visible {
  opacity: 0;
  visibility: hidden;
  transition: all 200ms cubic-bezier(.26,.88,.44,.96);
}

.u-section-visible.yay { visibility: visible; opacity: 1; }


/**
 * Clearfix
 *
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    contenteditable attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that are clearfixed.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */
.cf:before,
.cf:after {
    content: " "; /* 1 */
    display: table; /* 2 */
}

.cf:after {
    clear: both;
}

/**
 * For IE 6/7 only
 * Include this rule to trigger hasLayout and contain floats.
 */
.cf {
    *zoom: 1;
}
