/* 
 * Base styles
 */
.nav-item {
	font-size: 1.2rem;
	font-weight: 400;
	letter-spacing: 0.04em;
	text-transform: uppercase;

	a, a:hover { 
		color: var(--text-color);
	}
}

/*
 * Logo
 */

.logo {
	font-weight: 700;

	svg {
		height: auto;
		transition: width 200ms cubic-bezier(.26,.88,.44,.96);

		path {
			fill: var(--text-color);
		}
	}

	@media (--to-normal) {
		svg { width: 30px; }
	}

	@media (--normal) {
		svg { width: 45px; }
	}
}

/*
 * Default navigation styles
 */

.nav-bar {
	position: relative;

	@media (--to-normal) {
		background-color: var(--background-color);
		padding: 10px 0;
	}

	@media (--normal) {
		/* height: var(--nav-height); */
		height: 90px;
		transition: height 200ms cubic-bezier(.26,.88,.44,.96);

		display: flex;
		align-items: center;
	}
}

.nav {
	& ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	& li { 
		& a { padding-bottom: 3px; }
		& a.disabled-nav { opacity: 0.46; }
		& a.active-nav, &.active-nav a { border-bottom: 1px solid; padding-bottom: 3px; }
	}
}

.nav__sub {
	display: none;
	
	.active-nav + & {
		display: block;
	}
}

