/*
 * Main top navigation element
 */
.nav--main {
	@media (--to-normal) {
		display: none;

		& > ul {
			margin-top: 20px;
		}

		& > ul > li {
			padding: 10px 0;
			text-align: center;
		}

		.nav__sub {
			padding-top: 10px;

			li {
				padding: 5px 0;
			}
		}
	}

	@media (--normal) {
		margin-left: auto;
		
		& > ul {
			display: flex;
		}

		li + li {
			margin-left: var(--nav-item-gap);
		}

		.nav__sub {
			position: relative;
			/* height: 27px; */
			
			ul {
				position: absolute;
				top: 14px;
				display: flex;
			}

			.nav-item {
				font-size: 1rem;
			}
		}
	}
}

/* 
 * Festival artist block side navigation block
 */

.nav--side {
	font-size: 12px;

	& > div { float: left; }
	& > div:first-child { float: right; }
	
	& .button { 
		position: fixed;
		float: none;
		margin-left: 30px;
	}

	& .button--top { top: 80px; }
	& .button--bottom { bottom: 30px; }
}

@media (--to-normal) {
	.nav--side {
		& .button {
			background-color: #fff;
			
			left: 50%;
			transform: translateX(-50%);
			padding-top: 10px;
			padding-left: 15px;
			padding-right: 15px;

			margin: 0;

			box-shadow: 0px 6px 10px 0px rgba(107,106,107,0.6);
		}

		& .button--top { top: 40px; }
		& .button--bottom { bottom: 0; }
		& .button:after { left: 50%; transform: translateX(-50%); }
	}
}

@media (--small) {
	.nav--side { font-size: 14px; }
}

@media (--medium) {
	.nav--side { font-size: 16px; }
}

/*
 * Language menu style
 */

.nav--lang {
	ul {
		display: flex;
	}

	& li {
		&::after {
			content: '\b7';
			display: inline-block;
			position: relative;
			top: -1px;
			margin: 0 5px;
			font-weight: bold;
		}

		&:last-child::after {
			content: none;
		}
	}

	@media (--to-normal) {
		display: none;
		margin-top: 20px;

		ul {
			justify-content: center;
		}
	}

	@media (--normal) {
		margin-left: 40px;
	}
}

/* 
 * Slide up for sub menu in festival page
 */
@media (--normal) {
	.-h-slide {
		.nav--main, .nav--lang {
			transform: translateY(-12px)
		}
	}
}

/*
 * Mobile navigation
 */
.burger {
	position: absolute;
	top: 20px;
	right: 0;
	z-index: 2;

	display: block;
	width: 66px;
	padding: 0 28px 0 0;

	-webkit-transform: translate3d(0, 0, 0);
	-webkit-tap-highlight-color: transparent;

	font-size: 1.2rem;
	text-align: right;
	color: var(--default-color);

	& .icon::after {
		content: "\e20e";

		position: absolute;
		top: -5px;

		display: inline-block;
		margin-left: 5px;

		font-size: 1.8em
	}
}

@media (--normal) {
	.burger { display: none!important; }
}

/*
 * Style mobile and main menu
  * when the menu is open in the mobile view
 */

.menu-open {
	overflow: hidden;

	& .nav-bar {
		height: 100vh;
	}
	
	& .nav--main,
	& .nav--lang {
		display: block;
	}

	& .burger .icon::after {	content: "\e070"; }

	/* & .section__main { z-index: 0; } */
}


/*
 * Toggler styling
 */

.toggler {
	padding: 1.6em 5%;
	margin: 1.4em auto 0;

	border-bottom: 1px solid;
	/* & .section__main { z-index: 0; } */
}

.toggler h2 {
	font-size: 18px;
  margin: 0;
  /* text-align: center; */

  /* @media (--small) { font-size: 22px; } */
}

