/* Image reset */
img {
	max-width: 100%;
	vertical-align: top;
	height: auto;
	margin: 0;
	padding: 0;
}

/* Padding-bottom hack + lazy load */
figure {
	margin: 0;
	height: 1px;
	position: relative;
	overflow: hidden;

	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

figure img {
	position: absolute;
	top: 0;
	left: 0;
	max-width: 100%;
}

.artist__image { overflow: visible; }
.artist__image img { width: 100%; }

/* Velocity animate fade in - figure img.lazy-loaded, using velocity js */
.supports_js .lazyload,
.supports_js .lazyloading {
    opacity: 0;
}

.supports_js .lazyloaded {
    opacity: 1;
    transition: 250ms ease-in-out opacity;
}

.supports_no-js img.lazyload {
    display: none;
}