.news-wrapper {
	--border-color: #e2e2e2;
}

.theme-dark .news-wrapper {
	--border-color: #fff;
}

.news-wrapper {
	position: relative;

	font-size: 14px;
	line-height: 20px;
  background: var(--background-color);

	border-bottom: 2px solid var(--border-color);
}

.news-header {
	position: absolute;
	width: 100%;
  max-width: 30rem;

  transform: translateY(-98%);
}

.news-content {
	padding: 64px 0 60px;
}

.news-header h1 {
	margin: 0;
	padding: 0;

	font-size: 1em;
  line-height: 50px;
	text-align: center;
	text-transform: uppercase;
  letter-spacing: 0.12em;

  background: var(--background-color);
}

.news__entry {
	display: flex;
	flex-direction: column;

	width: calc(100% - 40px);
	max-width: 50rem;

	margin: 0;
	padding: 0 20px 15px;

	/* & .media { margin-bottom: 2em; } */

	& h2 {
		padding: 0;
		margin-bottom: 0;
	}

	& .text {
		margin-bottom: 1em;
	}

	& .news__date {
		margin-bottom: 1em;
	}

	& .news__moreLink {
		margin-top: auto;
		align-self: flex-end;
	}

	& .news__mainLink {
		text-decoration: none;
		border: none;
	}
}

@media (--medium) {
	.news__entry {
		padding: 0 60px;
		margin-bottom: 20px;
	}
}

@media (--normal) {
	.news-header { max-width: 54rem; }
	.news-wrapper { font-size: 16px; line-height: 22px; }

	.news-header h1 { line-height: 80px; }

	.news__entry { 
		width: auto;
		max-width: none;
		border-left: 1px solid var(--border-color);

		&:first-child {
			border-left: none;
		}

		& .media {width: 40rem; }
		& .text { width: 40rem; }
	}

	.news__entry.pos--left {
	
		& .media {
			float: left;
			width: 50rem;

			margin-right: 2em;
		}

		& .media + .text {
			float: left;
			max-width: 40rem;

			background: var(--background-color);
		}
	}
}