
:root {

/*
 * Typography
 */
	--base-font-scale: 0.625;

	--base-lineheight: 1.4285;
	--base-font-size: calc(16px * var(--base-font-scale));

	--base-font-family: 'Replica', Helvetica, Arial;

/*
 * Colors
 */
--color-black: rgba(0, 0, 0 , 1);
--color-white: rgba(255, 255, 255, 1);

--default-color: rgba(0, 0, 0 , 1);
--text-color: rgba(0, 0, 0 , 1);
--background-color: rgba(255, 255, 255, 1);

/* Bright red */
/* --color-accent: rgb(207, 11, 5); */

/* Bright green */
--color-accent: #00e2d1;

/* --fColor: rgba(255, 38, 48, 1); */
/* --fActiveColor: rgba(var(--fColor), 0.8); */
/* --fInactiveColor: rgba(var(--fColor), 0.2); */

/* --eColor: rgba(35, 43, 242, 1); */
/* --eActiveColor: rgba(var(--eColor), 0.8); */
/* --eInactiveColor: rgba(var(--eColor), 0.2); */

/* --default: rgba(0, 0, 0, 1); */
/* --defaultActive: rgba(var(--default), 0.8); */
/* --defaultInactive: rgba(var(--default), 0.2); */
/* --inactive: rgba(var(--default), 0.1); */

/* --textColor: rgba(0, 0, 0, 1); */
	
	/* Nesletter form subscription status */
	--color-form-status: #3c6126;

	/* Spacing */
	--section-margin: 80px;
	
	--nav-height: 90px;
	--nav-item-gap: 2em;
}

.theme-dark {
	--default-color: rgba(255, 255, 255, 1);
	--text-color: rgba(255, 255, 255, 1);
	--background-color: rgba(0, 0, 0 , 1);

	/* Replace accent color red with white on dark backgrounds */
	--color-accent: rgba(255, 255, 255, 1); 
}
