
/* Section structure */

.main { padding-top: var(--nav-height); }
.festival-page .main { padding-bottom: 30px; }
.home .main { padding-top: 0; }

.section, .container {
	width: calc(100% - 36px);
	max-width: 650px;

	margin-left: auto;
	margin-right: auto;

	position: relative;
}

/* Basic bloc sizing */

.block {
	width: 100%;
	float: left;

	&--normal {
		width: 90%;
		margin-left: 5%;
		margin-right: 5%;
	}

	&--full {
		width: 100%;
	}
}


/* Structure - mediaqueries  */

@media (--small) {}

@media (--medium) {
	.block--narrow { width: 50%; }
}

@media (--normal) {
	.section, .container { width: calc(100% - var(--section-margin)); max-width: none; }
  .section__main { width: 57%; float: right; }
  .section__side { width: 40%; float: left; }
  .section__side-header { width: 100%; max-width: 50vw; }
}

@media (--wide) {
  .section__side-header { width: 100%; }
}

@media (--x-wide) {
	.section, .container, .nav-bar { max-width: 1450px; }

  .section__main { width: 60%; }
  .section__side { padding-right: 6%; }
  .section__side-header { max-width: 45vw; }
}
