
/* 
 * General single page styling
*/

.page {

	&__header {
		text-align: center;
		margin: 26px 0;
	}

	&__title {
		max-width: 22em;
		margin: 0 auto;
	}
}

@media (--small) {
	.page__header { margin-bottom: 38px; }
}

@media (--x-wide) {
	.page__header { margin-top: 46px; }
}

/* 
 * Specific archive page styling
 * ---------------------------------------------------------
 */

.archive__intro {
	margin-bottom: 20px;
}

.archive {
	

	&__event {
		margin-top: 15px;
		font-size: 14px;
		line-height: 24px;

		&.-center { text-align: center; }
	}

	&__event-header {
		margin: 0 0 1em;
		padding: 1em 0;

		text-align: center;
	}

	&__event h1 {
		margin: 0;

		font-size: 1em;
		font-weight: normal;
	}

	&__event .date {
		display: block;

		font-size: 12px;
		line-height: 18px;
		font-weight: normal;
	}

	&__event ul {
		list-style: none;

		margin: 0;
		padding: 0;
	}

	&__artist {	
		display: inline; 
		margin-right: 5px;
		
		font-size: 1em;
	}

	&__artist a { border: 0; }

	&__artist .sep:after { content: ","; }
}

.archive__type-event {
	
	overflow: hidden;

	padding: 15px 10px 0;
	border-top: 1px solid #c2c2c2;
		
	& .archive__artist a {
		display: inline;
		padding: 0;
		border: 0;
	}

	& .archive__artist .sep { display: inline; }
}

.archive__type-festival {
	padding: 10px 15px 20px;
	background-color: #efefef;
}

.archive__type-festival + .archive__type-event {
  margin-top: 0;
  border-top: 0;
}

.archive__type-festival + .archive__type-festival {
	margin-top: 2px;
}

@media (--small) {
	.archive-page .section { width: calc(100% - 80px); max-width: none; }
	.archive__event { font-size: 15px; line-height: 28px; }
	.archive__type-festival { padding: 10px 25px 20px; }
}


@media (--medium) {
	.archive-page .content-text { margin-bottom: 50px; }

	.archive__event.-center { text-align: left; }
	.archive__event h1 { font-weight: normal; }

	.archive__type-event {

		& .archive__event-header {
			margin: 0;
			padding: 0;

			min-width: 250px;
			float: left;
			
			text-align: left;
		}

		& ul { 
			width: calc(100% - 250px);
			float: left;
		}

		& .archive__artist { display: inline; }
	}

	.archive__type-festival {
		text-align: center;

		& .archive__artist {

			display: inline-block;
			padding: 0;
			margin: 0 10px 10px 0;
			
			& a {
				display: block;
		    padding: 10px;
		    border: 1px solid #c2c2c2;
		  }

		  & .sep { display: none; }
		}
	}

	.archive__type-festival.-no-link {
		& .archive__artist {
			padding: 10px;
			border: 1px solid #c2c2c2;
			opacity: .5;
		}
	}
}

@media (--x-wide) {
	.archive-page .section { max-width: 1450px; }
	.archive__event { font-size: 18px; line-height: 28px; }
}