
.section {

	&__main {
		position: relative;
		z-index: 10;

		margin: 0;

		&.u-back { z-index: 0; }
		& .block { position: relative; }
	}

	&__side {
		margin: 0 0 3rem;
		padding-top: 1rem;

		text-align: center;

		z-index: 50;

		& h1 {
			margin: 0;
		}
	}

	&__side-content {
		margin: 40px auto;
		max-width: 42rem;
	}

	&__lineup {
		line-height: 24px;
		font-weight: bold;
		text-transform: uppercase;
		text-align: center;

		display: flex;
		justify-content: center;
		align-items: flex-end;

		@media (--to-small) {
			padding-left: 12px;
			padding-right: 12px;
		}

		& .lineup__column {
			flex: 1;

			&:not(:last-child) {
				margin-right: 20px;
			}
		}

		& ul { 
			list-style: none;
			margin: 0;
			padding: 0;
		}

		& .lineup__heading { font-size: .48em; line-height: 1.6; }

		& .lineup__entry {
			line-height: 1.24;
			font-size: 1.24em;
			font-weight: 500;

			text-align: left;

			/* &--normal {}
			&--normal p.small { font-size: .52em; line-height: 1; }
			
			&--small { margin-bottom: 8px; font-size: .6em; line-height: 1.3; }
			&--small p.small { font-size: .7em; line-height: 1.5; } */

			& p { margin: 0; }
			
			& > a {
				/* color: #fb0001; */
			}

			& .meta {
				margin-left: 6px;
				display: inline-block;
				position: relative;
				top: -0.38em;
			}
		}
	}

	&__content { 
		max-width: 30em;
		margin: 0 auto;

		& .content-text { font-weight: normal; }
		& > div { margin-top: 2em; }
	}
}

.lineup-header /*, .nav--side */ {
	font-size: 12px;
	font-weight: bold;
	letter-spacing: 0.12em;
	text-transform: uppercase;
}

@media (--to-small) {
	.section__lineup {
		display: block;
	}
}

@media (--to-normal) {
	.section__main { margin-bottom: 60px; }
	.section__main .block { margin-top: 0 !important; }

	.section__lineup { 
		font-size: 18px; 

		& .lineup__column { 
			/* display: inline; */
			/* float: none; */
			/* width: 100%; */
			/* margin: 0 auto;  */

			/* & ul { display: inline; } */
		}
	
		& .lineup__entry { 
			line-height: 1.3;
			font-size: 1em;
			/* display: inline-block;  */
			/* vertical-align: middle;  */
			/* margin: 0 8px 10px; */
		}
	}
}

@media (--medium) {
	.section__content { max-width: none; padding-left: 5%; }
	.section__content > div { max-width: 30em; }
}

@media (--normal) {

	.section__side {
		margin-top: 20px;
		margin: 0;
		padding: 0;

		text-align: left;
		border: none;

		&-content {
			margin: 20px 0 0;
			max-width: 22em;
		}
	}

	.section__side.u-stick { position: -webkit-sticky; position: sticky; top: 80px; margin-bottom: 80px; }
	.section__side-header.u-stick { position: -webkit-sticky; position: sticky; top: 60px; margin-bottom: 20px; }

	.single .section__main .block { background-color: var(--background-color); }

	.section__content > div { max-width: 36em; }
}

@media (--wide) {
	.section__side-content { max-width: 47rem; }
	.section__lineup { font-size: 20px; line-height: 25px; }
}

@media (--x-wide) {
	.section__side-content { max-width: 48.5rem; }
	.section__lineup { font-size: 25px; line-height: 32px; }
}
