:root {
	--font-size-xsmall: 8px;
	--font-size-small: 10px;
	--font-size-regular: 12px;
	--font-size-large: 30px;
	--font-size-xlarge: 27px;
}

@media (--medium) {
	:root {
		--font-size-small: 12px;
		--font-size-regular: 14px;
	}
}

@media (--normal) {
	:root {
		--font-size-small: 12px;
		--font-size-regular: 14px;
		--font-size-large: 28px;
		--font-size-xlarge: 36px;
	}
}

@media (--wide) {
	:root {
		--font-size-small: 14px;
		--font-size-regular: 16px;
		--font-size-large: 32px;
		--font-size-xlarge: 48px;
	}
}

strong {
	font-weight: 700;
}

.xs { 
	font-size: var(--font-size-xsmall);
	text-transform: uppercase;
}

.s { 
	font-size: var(--font-size-small);
}

.r { 
	font-size: var(--font-size-regular);
}

.l { 
	font-size: var(--font-size-large);
}

.xl { 
	font-size: var(--font-size-xlarge);
	line-height: 1;
}

.w-300 { font-weight: 300; }
.w-400 { font-weight: 400; }
.w-500 { font-weight: 500; }
.w-700 { font-weight: 700; }
.w-900 { font-weight: 900; }

.caps {
	text-transform: uppercase;
}

.floatingDebugContainer {
	position: fixed;
	bottom: 20px;
	left: 20px;
	padding: 0px 10px 30px;
	z-index: 100;

	outline: 1px solid #eaeaea;
	background: white;

	display: flex;
	flex-wrap: wrap;

	& > div {
		padding: 0 20px;
		
		&:not(:last-child) {
			border-right: 1px solid #eaeaea;
		}
	}
}



a {
	color: inherit; /* 000 */
	text-decoration: none;

	transition: opacity 120ms cubic-bezier(0.4, 0, 1, 1);

	&:hover { 
		opacity: 0.66;
	}

	&.-noHover:hover {
		opacity: 1;
	}
}

.content-text a {
	padding-bottom: 1px;
	border-bottom: 1px solid;
}

p {
	margin: 1rem 0 0;
}

h2 { text-decoration: none; }

.side-content {
	font-size: 11px;

	font-weight: 400;
	letter-spacing: 0.03em;
}

.side-content h4,
.nav h4 {
    color: rgba(0, 0, 0, .2);
    margin: 10px 0 5px
}

.side-content.active h4,
.nav.active h4 {
    color: var(--color-text); /* 000 */
}

.nav.nav-main {
	font-size: 12px;
	font-size: 1.2rem;

	@media (--wide) {
		font-size: 14px;
		font-size: 1.4rem;
	}

	@media (max-height: 800px) {
		font-size: 12px;
        line-height: 17px;
	}
}

.meta { font-size: 12px; line-height: 20px; white-space: nowrap; }

.event .meta { font-size: 12px; line-height: 12px; }
@media (--wide) { 
	.event .meta { font-size: 12px; line-height: 20px; }
}

.event__meta { font-size: 12px; line-height: 16px; }


.meta-block { font-size: 12px; line-height: 16px; }
.meta-block__content { font-size: 12px; line-height: 20px; }

.link-block__title { font-size: 12px; line-height: 16px; }

/*
 * Section side & header
 */

.section {

	&__side, &__header {
		font-size: 14px;
        line-height: 21px;
		font-weight: normal;		
	}

	&__side h1, &__header h1 {
		font-size: 22px;
		letter-spacing: 0.06em;
		text-transform: uppercase;
	}
}

@media (--small) {
	.section {
		&__side, &__header { font-size: 16px; line-height: 22px; }
	}
}

@media (--normal) {
	.section {
		&__side, &__header { font-size: 18px; line-height: 24px; }
		&__side h1, &__header h1 { font-size: 3rem; }
	}
}


/*
 * Artist block
 */


.block__header-item {
  text-transform: uppercase;
  font-size: 12px; line-height: 16px;

  @media (--x-wide) { 
		font-size: 13px;
		line-height: 18px;

		.-small { font-size: 26px; line-height: 26px; }
	}
}

.page__title {
	text-transform: none;
	font-size: 18px; line-height: 20px;

	@media (--small) { font-size: 22px; line-height: 26px; }
	@media (--wide) { font-size: 24px; line-height: 28px; }
	@media (--x-wide) { font-size: 30px; line-height: 36px; }
}

.artist__title {
  text-transform: none;
  font-size: 18px; line-height: 18px;

  @media (--small) { font-size: 22px; line-height: 22px; }
  @media (--wide) { font-size: 24px; line-height: 24px; }
  @media (--x-wide) { font-size: 30px; line-height: 30px; }
}


.block--narrow {
	& .block__header-item {
	  @media (--x-wide) { font-size: 12px; line-height: 16px; }
	}

	& .artist__title {
		@media (--wide) { font-size: 18px; line-height: 18px; }
		@media (--x-wide) { font-size: 22px; line-height: 22px; }
	}
}


.event {
	&__intro { font-weight: normal; }
	&__artist_content { font-weight: normal; }
	&__artist_content h2 { font-size: 16px; line-height: 18px; }

	@media (--small) {
 		&__intro 	{ font-size: 15px; line-height: 21px; }
	}

	@media (--x-wide) {
 		&__intro 	{ font-size: 18px; line-height: 24px; }

		&__artist_content 		{ font-size: 16px; line-height: 21px; }
		&__artist_content h2 	{ font-size: 18px; line-height: 20px; }
	}
}

/*
 * Event block (upcoming events)
 */


.event__artist-title {
	font-size: 22px; line-height: 18px;
	@media (--medium) { font-size: 22px; line-height: 26px; }
	@media (--wide) { font-size: 28px; line-height: 30px; }
	@media (--x-wide) { font-size: 38px; line-height: 38px; }
}

.event__title {
	font-size: 16px; line-height: 18px;

	@media (--wide) { font-size: 17px; line-height: 18px; }
}

.content-block {
	font-size: 14px; line-height: 21px;
}

#program .time {
	font-size: 12px; line-height: 22px;
}

@media (--small) {
	.content-block { font-size: 15px; line-height: 21px; }
}

@media (--wide) {
    #program .time { font-size: 12px; line-height: 24px; }
}

section {
	h6 { font-size: 12px; margin: 10px 0; }
}

.link-button {
	font-size: 1rem;
	text-transform: uppercase;
}

.event__link {
	font-size: 1.2rem;
	text-transform: uppercase;
}

@media (--normal) {
	.link-button { font-size: 1.2rem; }
}



/* 
 * Main long text block layout
 */

.embed, .video-embed { margin-top: 2em; }

.embed + .embed { margin-top: 2em; }
.video-embed + .video-embed { margin-top: 2em; }

.embed + .content-text { margin-top: 2em; }
.video-embed + .content-text { margin-top: 2em; }

.content-text p:first-child { margin-top: 0; }


/* 
 * Setup external webfonts
 */

@font-face {
  font-family: 'Riposte';
  src: url("fonts/riposte-bold.eot") format('embedded-opentype'),
			 url("fonts/riposte-bold.woff2") format('woff2'),
       url("fonts/riposte-bold.woff") format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Riposte';
  src: url("fonts/riposte-medium.eot") format('embedded-opentype'),
       url("fonts/riposte-medium.woff2") format('woff2'),
       url("fonts/riposte-medium.woff") format('woff');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Riposte';
  src: url("fonts/riposte-regular.eot") format('embedded-opentype'),
       url("fonts/riposte-regular.woff2") format('woff2'),
       url("fonts/riposte-regular.woff") format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'icomoon';
  src: url("fonts/icomoon.eot") format('embedded-opentype'),
       url("fonts/icomoon.woff") format('woff'),
       url("fonts/icomoon.ttf") format('truetype'),
       url("fonts/icomoon.svg") format('svg');
  font-weight: normal;
  font-style: normal;
}

