/* Icon font styles */

[class^=icon-],
[class*=" icon-"],
[class^=icon],
[class*=" icon"] {
    font-family: icomoon;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}
