.fp-hero__wrapper {
  display: flex;
  align-items: center;
  background-color: #ececea;
  padding: 20px;
  justify-content: center;
  z-index: 0;
}

.fp-hero__image {
  width: 100%;
  height: auto;
  max-width: 1200px;
  display: block;
  margin: 0 auto;
}

.mono-2021 {
   @media (--normal) {
    position: relative;
    width: calc(100% - 36px) !important;
    margin: 0 auto !important;
  }

  @media (--wide) {
    width: calc(100% - 80px) !important;
  }

  .festival-hero__wrapper {
    position: absolute;
    width: 100%;
    height: 100%;

    z-index: -1;
  }

  .fp-hero__wrapper {
    background: url('/assets/img/mono-background-small.jpg');
    background-size: cover;
    background-position-x: center;

    z-index: 0;
  }

  .fp-hero__image {
    width: 25%;
  }

  @media (--medium) {
    .fp-hero__wrapper {
      background-image: url('/assets/img/mono-background.jpg');
    }
  }
}

.ekko-2021 {

  .fp-hero__wrapper {
    /* outline: 1px solid blue; */

    display: grid;
    grid-template: "m m m m m m" "c c c c c c" "b b b b b b" "f f f f f f";
    grid-template-columns: repeat(6, 1fr);
    

    height: auto;
    padding: 0;
    /* padding: 40px 0 60px; */
    background: none;

    @media (--normal) {
      grid-template: "m m m m m m c c c c c c" "b b b b b b f f f f f f";
      grid-template-columns: repeat(12, 1fr);
      /* padding: 80px 0; */
    }
  }

  .festival-page & .fp-hero__wrapper {
    @media (--normal) {
      grid-template: "m m m m m c c c c c c c" "b b b b b b f f f f f f";
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &:not(.-tight) .fp-hero__wrapper {
    height: auto;
    padding: 40px 0 60px;

    @media (--normal) {
      padding: 80px 0;
    }
  }

  .fp-hero__content {
    grid-area: c;

    border: 2px solid var(--color-accent); /* CF0B05 */
    color: var(--color-accent); /* CF0B05 */
    text-transform: uppercase;

    margin-top: -2px;

    @media (--normal) {
      margin-top: 0px;

      position: relative;
      left: -2px;
      width: calc(100% + 2px);
    }
    
    padding: 32px;
    align-self: stretch;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    & > h1, & > h2, & > p {
      margin: 0;

      text-transform: uppercase;
      font-size: 1.8rem;

      @media (--to-normal) {
        font-weight: 800;
      }

      @media (--normal) {
        font-size: 3.2rem;
      }

      @media (--m-wide) {
        font-size: 4.2rem;
      }
    }

    .fp-hero__lineup {
      a {
        color: inherit;
      }

      p:first-child {
        margin-top: 0;
      }

      padding: 1.8rem 0;
      font-size: 1.8rem;

      strong {
        font-weight: 800;
      }

      @media (--normal) {
        padding: 3.2rem 0;
        margin-bottom: auto;
      }
    }
  }

  .festival-page & .fp-hero__content .fp-hero__lineup {
    font-size: 1.6rem;
  }

  .fp-hero__image-wrapper {
    /* grid-column: 1;
    grid-row: 1; */
    grid-area: m;
  }
  
  .fp-hero__image {
    width: 92%;
    height: auto;
  }

  .fp-hero__image-background {
    /* grid-column: 1;
    grid-row: 1; */
    grid-area: m;
  }

  .fp-hero__button {
    border: 2px solid var(--color-accent); /* CF0B05 */
    color: var(--color-accent); /* CF0B05 */

    & a {
      color: var(--color-accent); /* CF0B05 */
      text-transform: uppercase;

      font-size: 1.8rem;

      @media (--normal) {
        font-size: 2.4rem;
      }
    }

    margin-top: -2px;
    align-self: stretch;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .fp-hero__ticket-button {
    grid-area: b;
  }

  .fp-hero__fest-button {
    grid-area: f;

    @media (--normal) {
      margin-left: -2px;
    }
  }

  .fp-test {
    grid-column: b-start / span 1;
    grid-row: 3;
    visibility: hidden;

    &.-fest {
      grid-column: f-start / span 1;
      grid-row: 4;
    }

    @media (--normal) {
      grid-row: 2;
    }
  }
  
  svg {
    width: 100%;
    height: auto;

    * {
      fill: var(--color-accent); /* E53E24 */
      stroke: var(--color-accent);
    }
  }
}