:root {
  --white: #FFFFFF;
  --red: rgb(207, 11, 5); /* E53E24 */

  /* Grid settings */
  --grid-width: 1200px;
  --col-count: 6;
  --row-height: calc(var(--grid-width) / var(--col-count));
  --media-spacing: 15px;

  /* Grid border */
  --border-color: var(--red);
  --border: 3px solid var(--border-color);
}

@media (--normal) {
  :root {
    --media-spacing: 40px;
  }
}

.u-mv_push {
  margin-top: -3px !important;
  margin-bottom: -3px !important;
}

.arena {
  /* Grid border */
  --border-color: var(--white);
  --border: 3px solid var(--border-color);
}

.header-element {
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: var(--media-spacing);

  @media (--normal) {
    padding: calc(var(--media-spacing) * 0.5) var(--media-spacing);
  }
}

.header-grid {
  display: grid;
  grid-template-columns: repeat(var(--col-count), 1fr);
  grid-template-rows: minmax(var(--row-height), auto);

  & > .header-element {
    grid-column: span 4 / -1;

    border: var(--border);
    &.-no-top {
      border-top: none;
    }
  }

  &:not(.side-left) {
    margin-left: -3px;
  }

  &.side-left > .header-element {
    grid-column: 1 / span 4;
  }

  &.expand > .header-element {
    grid-column: span 4 / -1;
  }

  &.expand.side-left > .header-element {
    grid-column: 1 / span 4;
  }

  @media (--to-normal) {
    &.full > .header-element {
      grid-column: 1 / -1;
    }
  }

  @media (--normal) {
    & > .header-element {
      grid-column: span 3 / -1;
      /* padding: 30px; */
    }

    &.side-left > .header-element {
      grid-column: 1 / span 3;
    }
  }
}

.media-grid {
  display: grid;
  /* grid-template-columns: 40px repeat(2, 1fr) 40px;
  grid-template-rows: 40px auto 100px; */

  grid-template-columns: var(--media-spacing) repeat(2, 1fr) var(--media-spacing);
  grid-template-rows: calc(var(--media-spacing) * 1.5) 1fr calc(var(--media-spacing) * 2);

  /* Test */
  min-height: 65vh;

  .media-container {
    grid-row: 2 / span 1;
    grid-column: 2 / -2;
    align-self: center;
    justify-self: center;

    margin: 0 auto;
    width: 100%;
  }

  .arena-content-container {
    grid-row: 1 / -1;
    grid-column: 1 / -1;

    border: var(--border);
  }

  .project-container {
    grid-row: 1 / -1;
    grid-column: 1 /span 2;
  }

  .arena-content-container {
    display: flex;
    flex-direction: column;

    margin: 0;
    padding: var(--media-spacing);
    
    & .title-container {
      margin: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: column-reverse;
    }

    & .tags-container {
      margin-bottom: 8px;
    }

    & .tags-container a {
      display: inline-block;
      font-size: var(--font-size-small);

      &:not(:last-child) {
        margin-right: 4px;
      }
    }

    [data-hide-content] {
      display: none;
    }

    & .description-container {
      max-width: 54ch;
      margin: auto 0 0;
    }

    & a {
      text-decoration: underline;
    }
  }

  @media (--normal) {
    height: calc(100vh - var(--nav-height) - 20px);
    min-height: none;
    max-height: 1050px;

    .media-container {
      grid-row: 2 / span 1;
      grid-column: 2 / -2;
    }

    .arena-content-container {
      grid-column: span 2 / -1;

      padding: calc(var(--media-spacing) * 0.5) var(--media-spacing);

      & .title-container {
        height: 100%;
      }

      & .description-container {
        margin-top: 1rem;
      }
    }
  }

  @media (--normal) {
    .media-container {
      width: 110vh;
      max-width: calc(100vw - var(--section-margin) - calc(var(--media-spacing) * 2));
    }
  }

  @media (--x-wide) {
    .media-container {
      width: calc(100vh * 1.1); /* TODO: calculate width based on max height */
      max-width: 1370px;
    }
  }
}

.arena:not(.isPlaying) .arena-content-container.with-content:hover {
  background: #fff;
  color: #000;
  z-index: 100;

  & a {
    color: #000;
  }
  
  & [data-hide-content] {
    display: block;
  }
}

.media-grid.reverse {
  .media-container {
    grid-column: 2 / -2;
  }

  .arena-content-container {
    grid-column: 1 / -1;
  }

  .project-container {
    grid-row: 1 / -1;
    grid-column: 1 / -1;
  }

  @media (--normal) {
    .media-container {
      grid-column: 2 / -2;
    }

    .arena-content-container {
      grid-column: 1 /span 2;
    }

    .project-container {
      grid-row: 1 / -1;
      grid-column: 1 /span 2;
    } 
  }
}

.arena {  
  background: #000;
  color: #fff;

  padding-bottom: 20px;

  /* Grid section */
  h1, h2 {
    margin: 0;
  }

  font-size: var(--font-size-regular);
  font-weight: 400;

  .project-title {
    margin: 0;

    font-size: inherit;
    font-weight: inherit;
    line-height: 1.26;
    text-transform: uppercase;
    letter-spacing: 0.045em;

    & a {
      text-decoration: none;
    }
  }

  .project-tag {
    font-size: var(--font-size-small);
    text-transform: uppercase;
  }

  .next-project-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    height: 100%;
    width: 100%;

    & span, & a {
      font-weight: 400;
      letter-spacing: 0.045em;

      text-transform: uppercase;
    }

    & span {
      font-size: var(--font-size-small);
    }

    & a {
      margin-top: 10px;
      font-size: var(--font-size-regular);
      text-decoration: none;
    }
  }

  @media (--to-normal) {
    .next-project-container {
      padding: 8px 0;
    }
  }

  [data-playing-dim] {
    transition: opacity 600ms ease-in-out;
  }

  &.isPlaying {
    [data-playing-dim] {
      opacity: 0.15;
    }
  }

  /* End Grid section */

  .tag-project-list {
    font-size: var(--font-size-large);
    font-weight: 400;

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: calc(var(--row-height) / 1.5) minmax(var(--row-height), auto) auto;
  }

  .tag-block {
    border: var(--border);
    padding: 20px;

    font-size: var(--font-size-regular);

    margin-top: -3px;
    margin-left: 0;

    grid-row: auto / span 2;
    grid-column: auto / span 3;

    @media (--to-normal) {
      &:nth-child(2n+1):not(.tag-description) {
        margin-left: -3px;
      } 
    }

    @media (--normal) {
      &.-first {
        margin-top: 0;
        grid-row: 2 / span 1;
      }
      
      &:nth-child(2n) {
        margin-left: -3px;
      } 
    }
  }

  .tag-description {
    padding: 20px;

    background: #fff;
    color: #000;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    margin-top: 0;
    /* margin-left: 0; */

    grid-column: 1 / -1;

    @media (--normal) {
      grid-column: 1 / span 3;
      grid-row: 1 / span 2;
    }
  }

  .tag-description-container {
    font-size: var(--font-size-regular);
    line-height: 1.26;

    max-width: 54ch;
    margin-top: 20px;
  }

  .tag-project {
    .media-container {
      margin: 20px 0;
    }

    .media-container img {
      width: 100%;
    }
  }

  .next-project-container ul {
    list-style: none;
    padding: 0;
    margin: 24px 0 0;

    & li {
      padding: 6px 0;
      text-align: right;
    }

    @media (--normal) {
      margin-top: 36px;
    }
  }
}

a.content-link {
  color: white;
  text-decoration: underline;
}