/* ==========================================================================
   Remodal's default mobile first theme
   ========================================================================== */

/* Default theme styles for the background */

.remodal-bg.remodal-is-opening,
.remodal-bg.remodal-is-opened {
  -webkit-filter: blur(3px);
  filter: blur(3px);
}

/* Default theme styles of the overlay */

.remodal-overlay {
  background: rgba(43, 46, 56, 0.9);
}

.remodal-overlay.remodal-is-opening,
.remodal-overlay.remodal-is-closing {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.remodal-overlay.remodal-is-opening {
  -webkit-animation-name: remodal-overlay-opening-keyframes;
  animation-name: remodal-overlay-opening-keyframes;
}

.remodal-overlay.remodal-is-closing {
  -webkit-animation-name: remodal-overlay-closing-keyframes;
  animation-name: remodal-overlay-closing-keyframes;
}

/* Default theme styles of the wrapper */

.remodal-wrapper {
  padding: 10px 10px 0;
}

/* Default theme styles of the modal dialog */

.remodal {
   padding: 40px 35px 0;
   font-size: 18px;
   line-height: 24px;
   font-weight: normal;
   text-align: left;
   color: #2b2e38;
   background: #fff;

  @media (max-width: 539px) {
    position: fixed;
    top: 10px;
    width: calc(100% - 20px);
  }

  @media (min-width: 540px) {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);

    width: 500px;
    height: 500px;
  }

  @media (--medium) {
    left: auto;
    right: 20%;
    transform: translate3d(0, -50%, 0);
  }
}

.remodal.remodal-is-opening,
.remodal.remodal-is-closing {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.remodal.remodal-is-opening {
  -webkit-animation-name: remodal-opening-keyframes;
  animation-name: remodal-opening-keyframes;
}

.remodal.remodal-is-closing {
  -webkit-animation-name: remodal-closing-keyframes;
  animation-name: remodal-closing-keyframes;
}

/* Vertical align of the modal dialog */

.remodal,
.remodal-wrapper:after {
  vertical-align: middle;
}

/* Close button */

.remodal-close {
  position: absolute;
  top: 10px;
  right: 10px;

  display: block;
  overflow: visible;

  width: 25px;
  height: 25px;
  margin: 0;
  padding: 0;

  cursor: pointer;
  /* -webkit-transition: color 0.2s; */
  /* transition: color 0.2s; */
  text-decoration: none;

  color: #000;
  outline: 0;
  
  background: transparent;
}

/* .remodal-close:hover,
.remodal-close:focus {
  color: #fff;
} */

.remodal-close:before {
  font-size: 25px;
  line-height: 25px;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 25px;
  content: "\00d7";
  text-align: center;
}


/* Custom styles
   ========================================================================== */

#newsletter-modal {
  height: auto;
  padding-bottom: 35px;
}

.modal-content { }

.modal-form {
  margin: 30px 0 0;
  font-size: 14px;

  & p {
    line-height: 1.26;
  }

  & label { 
    display: block;
    font-size: 13px;
    font-weight: bold;
    color: #808287;
  }

  & input {
    font-family: var(--base-font-family);
    font-size: 15px;
  }

  & input[type=text],
  & input[type=email] {
    border: none;
    border-bottom: 1px solid;
    padding: 10px 10px 5px 0;
    width: 100%;
    margin: 0;
  }

  & input:focus { outline: none; }

  & fieldset label {
    font-weight: normal;
    color: #000;
  }

  & input[type=checkbox] {
    display: inline;
    width: auto;
    margin-right: 10px;
  }

  & .field-line {
    margin-bottom: 15px;
  }

  & .content__gdpr label + p {
    margin: 0;
    color: #808287;
  }

  & .gdpr-mergeRow {
    margin-top: 20px;
  }

  & .content__gdpr .mc_fieldset {
    border: none;
    min-height: 0;
    padding: 10px 0;
    border-bottom: 1px solid;
  }

  & .modal-button {
    display: block;
    width: 150px;
    height: 60px;

    padding-left: 15px;
    margin-top: 20px;
    
    border: 1px solid;

    font-size: 13px;
    line-height: 60px;
    background-color: #fff;
    
    float: right;

    &:hover {
      opacity: 0.6;
    }
  }

  & .response {
    line-height: 1.26;
  }

  & .error-response {
    color: red;
  }

  & .success-response {
    color: green;
  }

  & .form-status {
    margin-top: 35px;
    display: block;
    float: left;

    color: #cea49a;
  }

  &.status-ok .form-status { 
    color: var(--color-form-status);
  }
}

#mc_embed_signup .modal-form div.mce_inline_error {
  color: red;
  border: none;
  font-size: 13px;
  background: none;
  font-weight: normal;
  padding-left: 0;
}

#mc_embed_signup .modal-form input.mce_inline_error {
  border-color: #000;
}

#ticket-modal {

  height: auto;
  width: 600px;

  padding: 20px 0 0 10px;

  background-color: #f2f2f2;

  & .modal-content {
    margin-left: 15px;
    padding-right: 30px;
  }
}

#modal-frame {
  margin-top: 20px;
}

#ticket-modal, #newsletter-modal { display: none; }
.remodal-wrapper #ticket-modal, .remodal-wrapper  #newsletter-modal { display: block; }


/* Keyframes
   ========================================================================== */

 @-webkit-keyframes remodal-opening-keyframes {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes remodal-opening-keyframes {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes remodal-closing-keyframes {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes remodal-closing-keyframes {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}


@-webkit-keyframes remodal-overlay-opening-keyframes {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes remodal-overlay-opening-keyframes {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes remodal-overlay-closing-keyframes {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes remodal-overlay-closing-keyframes {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

