
.stamp {
  position: relative;

  &--item {}
  &--main { display: none; }
  &--fixed { display: none;}
  &--alt { padding: 15px 0; margin-top: 30px; text-align: center; }

  &--alt img { width: 140px; max-width: 200px; }

  @media (--small) {
    &--alt { margin-top: 0; }
    &--alt img { width: 160px; }
  }

  @media (--normal) {
    &--main { display: block; }
    &--alt { display: none; }
    &--item { transform: translatez(0); }

    & img { max-width: 100%; }

    display: block;
    width: 140px;

    /* position: absolute; */
    /* top: -40px; */

    opacity: 1;
    transition: opacity 100ms linear;
    will-change: opacity;
    z-index: -10;

    &.hidden { opacity: 0; }

    &--fixed {
      display: block;
      position: fixed;

      z-index: 60;
    }
  }

  @media (--wide) {
    width: 160px;
	}

	@media (--x-wide) {
		width: 190px;
	}
}


