/*
 * Breakpoints
 */

@custom-media --small (min-width: 480px);
@custom-media --to-small (max-width: 479px);

@custom-media --medium (min-width: 768px);
@custom-media --to-medium (max-width: 767px);

@custom-media --normal (min-width: 1045px);
@custom-media --to-normal (max-width: 1044px);

@custom-media --wide (min-width: 1150px);
@custom-media --to-wide (max-width: 1149px);

@custom-media --m-wide (min-width: 1200px);

@custom-media --x-wide (min-width: 1600px);
@custom-media --xx-wide (min-width: 2100px);


#debug {
  z-index: 9999999;
  position:fixed;
  bottom:0;
  right:0;
  padding:0.6rem 0.6rem 0.4rem;
  height:auto;
  background: #aaa;
  color: #fbfbfa;
  font-family: 'Consolas', Courier, Helvetica, Arial, sans-serif;

  font-size: 14px;
  line-height: 1;

  &:after{
      content:'default';

      @media (--small) { content:'small'; }
      @media (--medium) { content:'medium'; }
      @media (--normal) { content:'normal'; }
      @media (--wide) { content:'wide'; }
      @media (--x-wide) { content:'x-wide'; }
      @media (--xx-wide) { content:'xx-wide'; }
  }
}
