
/* Basic block style definition */

.block {

	&__content-wrapper {
		position: relative;
		width: 90%;
		height: 100%;
		margin: 0 auto;
	}

	&--normal &__content-wrapper {
		width: 100%;
	}

	&__header {
		width: 100%;
		margin: 0 auto;
		padding: 8px 10px;
	}

	&__header.-over {
		position: absolute;
		left: 0;
		right: 0;

		transform: translateY(-100%);

		background-color: var(--background-color);
	}

	&__header-item {
		text-align: center;
		width: 100%;

		& a:hover { opacity: 1; }

		& br { display: none; }
		& .location-separator { display: inline; }
		& .location-separator:before { content: ', '; }

		&.-left {}
		&.-center { padding: 10px 0 5px; }
		&.-right {}
	}
}

.home .block a:hover { opacity: 1; }

.event .block__header-item.-center { padding: 10px 0; }

@media (--medium) {


}

@media (--normal) {

	.block {
		&__content-wrapper { width: 100%; }
		&--full &__content-wrapper 		{ width: 90%; }
		&--narrow &__content-wrapper 	{ width: 90%; }
	}

	.single .block__header {
		padding-top: 23px;
		padding-bottom: 15px;
	}

	.single .-over {
		position: static;
		transform: none;
	}
}

@media (--wide) {

	.block {

		/* &--full, &--normal { */
		&:not(.block--narrow) {

			/* TODO: explore change to display: flex */
			& .block__header {
				display: table;
				table-layout: fixed;

				height: auto;
				min-height: 100px;

				padding: 10px 0;
			}

			& .block__header--small {
				min-height: 70px;
			}

			& .block__header-item {
				display: table-cell;
				vertical-align: middle;

				& br { display: inline; }
				& .location-separator { display: none; }

				&.-side { width: 20%; }
				&.-center { width: 60%; padding: 15px 20px; }
				&.-left { left: 0; text-align: right; }
				&.-right { right: 0; text-align: left; }
			}
		}
	}
}
