
/*
 * Single event page
 */

.event {

	&__content {}

	&__artist_content {
		position: relative;
		/* margin-top: 40px; */
		margin-top: 30px;

		& .content-text { max-width: 22em; }

		& h2 {
			border-bottom: 1px solid;
	    padding-bottom: 10px;
	  }

	  & .link-button {
	  	margin: 10px 0 0;

	  	font-size: 1.2rem;
	  	text-transform: none;
	  }
	}

  &__meta_header {
		margin-top: 15px;
		margin-bottom: 5px;

		opacity: .5;

		&:first-child { margin-top: 0; }

		&:before {
			content: ' | ';
	    margin: 0 5px;
	    position: relative;
	    top: -2px;
		}

		&:first-child:before {
			content: '';
			margin: 0;
		}

		&:after { content: ': '; }
	}

	&__link {
		display: inline-block;
		margin-left: 10px;
		margin-bottom: 10px;
		margin-top: 40px;
	}

  &__artist_side_content {
  	margin-bottom: 10px;

		& span { display: inline; }
    & br { display: none; }
	}
}


@media (--small) {

	/* .event__artist_content { margin-top: 50px; } */

	.event__artist_side_content {
		position: absolute;
  	right: 0;

  	width: 155px;

		margin: 0;
		padding: 0;
  	border: none;

		& span { display: block; }
  	& br { display: inline; }
	}

	.event__meta_header {
		&:before, &:after { display: none; }
	}
}

@media (--x-wide) {

	.event__artist_side_content { top: 55px; }
	.event__artist_content .content-text { max-width: 29em; }
}
