.festival-lineup {
  position: relative;

  padding: 3.2rem;

  border: 2px solid var(--color-accent);
  border-top: 0;

  @media (--normal) {
    padding-left: 0;
    padding-right: 0;
  }

  h2 {
    margin: 0;

    color: var(--color-accent); /* CF0B05 */
    text-transform: uppercase;
    font-size: 1.8rem;

    @media (--to-normal) {
      text-align: center;
      margin-bottom: 1.6rem;
    }

    @media (--normal) {
      font-size: 3.2rem;

      transform-origin: top left;
      transform: rotate(-90deg) translateX(-100%);

      position: absolute;
      top: 4rem;
      left: 3rem;
    }

    @media (--m-wide) {
      font-size: 4.2rem;
    }
  }

  p:first-child {
    margin-top: 0;
  }

  p {
    font-size: 1.4rem;
    line-height: 1.34; 
    font-weight: 400;

    @media (--small) {
      font-size: 1.5rem;
    }

    @media (--normal) {
      font-size: 1.8rem;
    }
  }
}
