.header {
	position: fixed;
	width: 100%;

	z-index: 200;

	background-color: rgba(var(--background-color), 0);
	will-change: background-color;
	transition: all 200ms cubic-bezier(.26,.88,.44,.96);
}

body:not(.theme-dark) {
	.header.-stay {
		background-color: var(--background-color);
		box-shadow: 0px 4px 10px -4px rgba(107,106,107,0.6);
	}
}

.theme-dark {
	.header.-stay {
		background-color: var(--background-color);
		border-bottom: 1px solid var(--default-color);
	}
}