
.main {

	& .content-block {
		/* margin-bottom: 40px; */

		& .column-wrapper { padding: 0px; max-width: none; }
		& .column { width: 100%; padding-bottom: 20px;	}

		@media (--small) {
			& .column-wrapper { display: flex; flex-wrap: wrap; }
			& .column { width: 50%; float: left; padding-bottom: 50px;	}
			& .column:nth-child(2n+1) { padding-right: 20px; }
		}

		@media (--medium) {
			& .column { width: 33%; float: left; padding-bottom: 50px;	}
			& .column:nth-child(2n+1) { padding-right: 20px; }
		}

		@media (--normal) {
			&--column { width: calc(50% - 20px); margin-right: 40px; float: left;  }
			&--column:nth-child(2n+2) { margin-right: 0; }
			/* &:last-child { min-height: calc(100vh - 70px); } */
		}
	}

	/*
	 * SECTIONS IN INDEX
	 * ---------------------------------------------------------
	 */


	& #billetter {

		& ul {
	    list-style: none;
	    padding: 0;
			margin: 0;
		}

		& li {
			margin-bottom: 10px;
			padding-bottom: 2px;
			border-bottom: 1px solid #efefef;

			display: flex;
			justify-content: space-between;
			align-items: center;

			& .ticketBlock_description {
				display: block;
				font-weight: 400;
			}

			& .ticketBlock_price {}
		}

		& .ticket-button {
			height: 75px;
		}
	}
}

#artister {
	margin: 60px 0 2px;
	
	@media (--normal) {
		margin-top: 80px;
	}
}

.program {

	& .column-wrapper {
    padding: 0 5% !important;
	}

	& ul {
		list-style: none;
		margin: 0 0 10px 0;
		padding: 0;
	}

	& ul li { overflow: hidden; }

	& span { display: block; }

	& a { display: inline; overflow: hidden; }

	& .date {
		margin-bottom: 10px;
		text-align: center;
	}

	& .location {
		margin-bottom: 6px;
		margin-top: 24px;
		font-size: 12px; line-height: 19px;
	}

	& .time { width: 40px; float: left; }
	& .title { max-width: calc(100% - 40px); float: left; }
}

section.program {
  margin-top: -2px;
	border: 2px solid var(--color-accent);

	h2 {
		text-align: center;
	}
}
