
.meta-block__row-container {
	padding: 10px 0;
	border-top: 1px solid;
}

.meta-block--top .meta-block__row-container {
	padding-bottom: 0;
}

@media (--small) {
	.meta-block {
		display: flex;
	}

	.meta-block__row-container {
		flex-grow: 1;
	}

	.meta-block--top .meta-block__row-container {
		padding-bottom: 10px;
		border-bottom: 1px solid;
	}
}

/* ==================================================== */

.meta-block {
	position: relative;
	width: 100%;
	margin: 2.5em 0 0;

	&__row {
		padding-left: 10px;
		margin-bottom: 8px;
	}

	&__title {
		opacity: .5;
	}

	&__content {}

	& .ticket-button {
		height: 75px;
		margin: 15px 0 0px;
	}

	& ul {
		list-style: none;
		padding: 0;
		margin: 0 0 10px;
	}

	& .program {
		margin-top: 10px;
		margin-bottom: 0;

		& .location {
			text-transform: normal;
			border: none;
		}

		& .location:first-child {	margin-top: 0; }

		& ul {
			margin: 0 0 10px;
			text-transform: none;

			&:last-child { margin-bottom: 0; }
		}

		& a { 
			display: inline;
			border: 0;
		}

		& .time {
			width: 35px;
			margin: 0 10px 0 0;
		}
	}
}

.meta-block--top {
	margin: 1em 0 2em;
}

.link-block + .meta-block { margin-top: 1em; }

@media (--small) {
	.meta-block {
		&__row {
			display: flex;

			padding-right: 0;
			padding-bottom: 0;
			margin-bottom: 4px;

			&:last-of-type { margin-bottom: 0; }
		}

		&__title {
			width: 95px;
			margin-bottom: 0;
		}

		& .program { margin-top: 0; }

		& .ticket-button {
			width: 75px;
	    height: 75px;

	    margin: 0 0 0 25px;
	    
	    text-align: right;
		}
	}

	.meta-block--top {
		margin-top: 2em;
	}

	.u-day-program {
		display: inline-block;
		max-width: 80px;
	}
}

@media (--wide) {
	.meta-block--top { margin: 4em 0 2em; }
}
