
.content {
	
	margin-bottom: 20px;

	/* & > div {
		max-width: 52rem;

		margin-left: auto;
		margin-right: auto;
	} */

	&-text-wrapper { max-width: 56ch; }

	& a { border-bottom: 1px solid; }

	& p:last-child { margin-bottom: 1em; }
}

.content-container > div {
	max-width: 52rem;

	margin-left: auto;
	margin-right: auto;
}

@media (--normal) {
	.content { margin-bottom: 50px; }

	.content-container {
		padding-left: 30px;
	}

	.content-container > div {
		max-width: 56rem;
	}
}

@media (--x-wide) {
	.content-container > div {
		max-width: 70rem;
	}
}


/* 
 * Typography
 * -----------------------------------------
 */

.content { font-size: 14px; line-height: 1.34; }

.content-text { font-weight: normal; }
.content-text h2 { font-size: 1em; letter-spacing: 0.02em; line-height: 1.4; text-transform: uppercase;  margin: 2em 0 1em; }
.content-text h4 { font-size: .8em; letter-spacing: 0.04em; line-height: 1.4; text-transform: uppercase;  margin: 1.5em 0 0; }
.content-text .small { font-size: .8em; line-height: 1.4; margin: 1.6rem 0 0; }
.content-text .tiny { font-size: .7em; line-height: 1.2; margin: 1rem 0 0; }

.content-text h4 + p.small { margin-top: 0; }
.content-text p:not(.small) + p.small { margin-top: 2em; }

@media (--small) {
	.content { font-size: 15px; }
}

@media (--x-wide) {
	.content { font-size: 18px; }
}