
.link-block {
	position: relative;
	width: 100%;
	padding: 1em 0 8px 10px;
	margin: 2em 0 0;

	border-top: 1px solid;

	&__title {
		opacity: .5;
		margin-bottom: 10px;
	}

	&__content {}

	& ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	& li { margin-bottom: 1em; }
	& li:last-child { margin-bottom: 0; }

	& a { display: inline; }
}

@media (--small) {

	.link-block {
		margin: 2em 0 0;

		&__title {
			width: 95px;
			float: left;

			margin-bottom: 0;
		}

		&__content {
			width: calc(100% - 95px);
			float: left;
		}
	}
}