
.section + .section { margin-top: 0; }

/* TMP change */
#prosjekter + .section { margin-top: 80px; }

.section-front {
  position: relative;

  width: 100%;
  max-width: none;
}

.section-wrapper {
  width: 100%;
  height: 54vh;

  position: relative;
  overflow: hidden;

  @media (--medium) {
    height: 76vh;
  }

  @media (--wide) {
    height: 86vh;
    min-height: 720px;
  }
}

/* .section-front .section-wrapper {
  position: sticky;
  top: 0;
} */

/*
 * Front page main visual
 */

.festival-hero {
  width: 100%;
  height: 100%;

  padding-top: 40px;

  display: flex;
  justify-content: center;
  align-items: center;

  /* @media (--normal) { */
    position: relative;
    /* width: calc(100% - 36px); */
    margin: 0 auto;
  /* } */

  @media (--wide) {
    /* width: calc(100% - 80px); */
  }
}

/* .festival-hero__wrapper {
  position: absolute;
  width: 100%;
  height: 100%;

  z-index: -1;
} */

.festival-hero__background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.festival-hero__header {
  font-size: 36px;
  text-align: center;

  & a {
    display: inline-block;
  }

  & h1 {
    font-size: 1em;
    letter-spacing: 0.02em;

    margin: 0;
  }

  & p {
    font-size: .68em;
  }
}

/* @media (--medium) {
  .section-front .link {
    font-size: 24px;
    line-height: 30px;
  }
} */

@media (--normal) {
  .festival-hero__header {
    font-size: 50px;
  }
}

.section__header {
  margin: 30px 0;
  text-align: center;
}

.festival-page .section__header,
.home .section__header {
  margin: 40px 0;
}

@media (--small) {
  .section__header { margin: 40px 0 60px; }

  .home .section__header { margin: 60px 0; }
}

@media (--medium) {
  .section__header { margin: 40px 0 80px; }

  .home .section__header { margin: 80px 0; }
}

@media (--normal) {
  .festival-page .section__header { margin: 80px 0; }
}

.section__header > div {
  margin: 30px auto 0;
}

.section__header-content {
  text-align: left;

  font-size: 28px;
  letter-spacing: 0.02em;
  line-height: 1;
  font-weight: 500;
  /* color: #fb0001; */

  margin-bottom: .6em;

  & h2 {
    font-size: 1em;
    margin: 0 0 0.5em;

    font-weight: inherit;
  }

  & p {
    font-size: 12px;
    line-height: 1.4;
    text-transform: uppercase;
  }
 
  @media (--medium) {
    font-size: 36px;
  }
}

.section__title {
  margin: 0;
}


#festival .section__main,
#prosjekter .section__main {
  margin: 0 auto;
  float: none;
}

#festival .section__featuredImage {
  margin: 0 0 4rem;

  @media (--medium) {
    margin: 2rem 0 6rem;
  }

  @media (--wide) {
    margin: 6rem 0 8rem;
  }
}


.festivalEntry-hero__wrapper {
  position: absolute;
  z-index: -1;
  margin: 0 auto;
  width: 90%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;

  transform: translateY(4%);

  @media (--medium) {
    width: 84%
  }

  @media (--normal) {
    width: 80%;
  }

  & img {
    width: 100%;
  }
}